import type { FC } from 'react';
import { Modal } from 'antd';
import MarkdownRender from '../MarkdownRender/MarkdownRender.tsx';
import { markdown } from './terms.md';

type TermsModalProps = {
  isOpen: boolean;
  onClose: () => void;
};

const TermsModal: FC<TermsModalProps> = ({ isOpen, onClose }) => {
  return (
    <Modal
      title="Terms & Conditions"
      open={isOpen}
      onCancel={onClose}
      onOk={onClose}
      okText="Accept"
      width={800}
      styles={{
        body: {
          padding: 0,
          maxHeight: '70vh',
          overflowY: 'auto',
        },
      }}
    >
      <MarkdownRender markdown={markdown} />
    </Modal>
  );
};

export default TermsModal;
